html {
  font-family: "Roboto", sans-serif;
  font-size: 12px;
}

* {
  box-sizing: border-box;
}

.header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  background-color: #fff;
  padding: 0 50px;
  align-items: center;
}

.nav__menu {
  display: flex;
}

.nav__item {
  list-style: none;
  display: block;
  text-transform: uppercase;
  font-weight: 500;
  position: relative;
}

.nav__link {
  text-decoration: none;
  color: #000;
  padding: 23px 0;
  display: block;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
}

.is-active::after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
}

.moyoLogo {
  margin-top: 5px;
}
